import {
  Text,
  Flex,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  AvatarGroup,
  Avatar,
} from "@chakra-ui/react";
import useUSDCPrice from "../hooks/useUSDCPrice";
import {
  formatBNPrice,
  formatBNUnits,
  getHoldings,
  getValueRate,
  shortenAddress,
  subBN,
} from "../helpers";

import { useTranslation } from "react-i18next";
import { ChainId, useEthers } from "@usedapp/core";
import { useStyles } from "../hooks/useStyles";

import { useWhitelistedStrategies } from "../hooks/useWhitelistedStrategies";
import useStrategyAgreementHoldings from "../hooks/useStrategyAgreementHoldings";
import useMetadata from "../hooks/useMetadata";
import useToken from "../hooks/useToken";
import { InstrumentsLogos } from "../assets/Instruments";
import { GainsValue, ShowValue, TableHead, TextValue } from "./TableCommon";
import { LogoIcon } from "../assets/MimicIcons";
import { useState } from "react";
import { Redirect } from "react-router-dom";

export function StrategiesTable({ agreementAddress }) {
  const { t } = useTranslation();
  const { buttonBorderStyle } = useStyles();

  const { chainId } = useEthers();
  const { data: whitelistedStrategies } = useWhitelistedStrategies();

  return (
    <>
      <Thead>
        <Tr>
          <Th borderBottom={buttonBorderStyle}>
            <Text color="white">STRATEGIES</Text>
          </Th>
          <TableHead>Strategy Allocation</TableHead>
          <TableHead>{t("totalUsd")}</TableHead>
          <TableHead simple={true}>{t("gainsUsd")}</TableHead>
        </Tr>
      </Thead>
      <Tbody>
        {whitelistedStrategies?.length > 0
          ? whitelistedStrategies.map((st) => {
              return (
                <TableRow
                  key={st?.id}
                  strategy={st}
                  chainId={chainId}
                  agreementAddress={agreementAddress}
                />
              );
            })
          : "No strategies"}
      </Tbody>
    </>
  );
}

function TableRow({ strategy, agreementAddress }) {
  const [redirectToStrategies, setRedirectToStrategies] = useState(false);

  const { data: holdings } = useStrategyAgreementHoldings(
    strategy?.id,
    agreementAddress
  );
  const { data: meta } = useMetadata(strategy?.id);

  const currentValue = getHoldings(
    holdings?.shares, // portfolio holdings
    holdings?.strategy?.lastRate?.totalShares,
    holdings?.strategy?.lastRate?.totalValue
  );

  const valueRate = getValueRate(
    currentValue,
    holdings?.strategy?.lastRate?.valueRate
  );

  const initalHoldings = getValueRate(
    holdings?.invested,
    holdings?.strategy?.lastRate?.valueRate
  );

  const totalGains = subBN(valueRate, initalHoldings);

  const { buttonBorderStyle, bgHover } = useStyles();
  const tk = useToken(strategy?.token?.id);
  const amountNormalized = formatBNPrice(valueRate, tk.decimals);
  const address = tk ? tk[ChainId.Mainnet] : strategy?.token?.id;

  const price = useUSDCPrice(address, amountNormalized);

  const totalGainsStrategy = formatBNUnits(totalGains, tk.decimals);

  const totalGainsUSD = totalGainsStrategy; //useUSDCPrice(tk[1], totalGainsStrategy); // hardcode mainnet to get assetPrice
  const isJoined = valueRate;

  // console.log(
  //   "StrategyTable vaultBalance",
  //   meta?.title,
  //   actualHoldings,
  //   holdings?.invested,
  //   "gains -> ",
  //   gainsStrategies
  // );

  // console.log("tt", meta?.tokens);
  if (redirectToStrategies) {
    return (
      <Redirect
        to={{
          pathname: "/manage/strategies",
        }}
      />
    );
  }

  return (
    <Tr
      key={strategy?.id}
      _hover={{
        bg: bgHover,
        cursor: "pointer",
      }}
      onClick={() => setRedirectToStrategies(true)}
    >
      <Td borderBottom={buttonBorderStyle}>
        {meta ? (
          <Flex wrap="wrap" align="center">
            <AvatarGroup max="4">
              {meta?.tokens &&
                meta?.tokens.map((inst) => {
                  return (
                    <Avatar
                      key={inst}
                      mr="1"
                      boxSize="30px"
                      src={InstrumentsLogos[inst]}
                      icon={<LogoIcon />}
                    />
                  );
                })}
            </AvatarGroup>
            <TextValue>{meta?.title}</TextValue>
          </Flex>
        ) : (
          <TextValue>{shortenAddress(strategy?.id)}</TextValue>
        )}
      </Td>

      {/* STRATEGY ALLOCATION */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={amountNormalized} /> {tk?.name}
      </Td>

      {/* TOTAL USD */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={price} price={true} />
      </Td>
      <Td align="center" borderBottom={buttonBorderStyle}>
        <GainsValue
          totalGainsStrategy={totalGainsStrategy}
          totalGainsUSD={totalGainsUSD}
          isJoined={isJoined}
        />
      </Td>
    </Tr>
  );
}
