import {
  Text,
  AvatarBadge,
  Avatar,
  Spinner,
  Flex,
  Badge,
} from "@chakra-ui/react";
import { isTheSameAddress, shortenAddress } from "../../helpers";
import useAgreement from "../../hooks/useAgreement";
import { useStyles } from "../../hooks/useStyles";

export const PortfolioLine = ({
  address,
  portfolioSelected,
  isOpen,
  size,
  color,
}) => {
  const { colorModalTitle } = useStyles();

  const { data: agreement, isLoading } = useAgreement(address);
  const match = isTheSameAddress(portfolioSelected, address);

  return (
    <>
      <Flex flexWrap="nowrap" maxH="40px" overflow="hidden" align="center">
        <Avatar
          boxSize={size ? "1.75rem" : "2rem"}
          name={address}
          src={require("../../assets/images/ph_dark.png").default}
          mr="1"
        >
          {match && isOpen && (
            <AvatarBadge boxSize="0.5em" border="none" bg="green.500" />
          )}
        </Avatar>
        <Text
          fontSize="1.1rem"
          ml={size ? "1" : "2"}
          mr={size ? "1" : "2"}
          color={color ? color : colorModalTitle}
          noOfLines="1"
        >
          {address && shortenAddress(address)} {agreement?.name ? "|" : null}
        </Text>
        <Text
          color={color ? color : colorModalTitle}
          fontSize="1.1rem"
          fontWeight="600"
          noOfLines="1"
          isTruncated
        >
          {agreement?.name ? ` ${agreement?.name}` : null}
        </Text>
        <AddressType address={address} />
        {isLoading && (
          <>
            | <Spinner size="xs" mx="2" />
          </>
        )}
      </Flex>
    </>
  );
};

const AddressType = ({ address }) => {
  const { data: agreement, isLoading } = useAgreement(address);
  if (agreement?.name) {
    return null;
  } else {
    return (
      <>
        {isLoading ? null : (
          <Flex px="2">
            <Badge colorScheme="violet">EOA</Badge>
          </Flex>
        )}
      </>
    );
  }
};
