import { getSubgraphqlURI } from "../helpers/enviroment";
import { useQuery } from "react-query";
import { request, gql } from "graphql-request";

const useStrategies = () => {
  return useQuery(["strategies"], () => fetchStrategies(), {
    refetchInterval: 10000,
  });
};

const fetchStrategies = async () => {
  const { strategies } = await request(
    getSubgraphqlURI(),
    gql`
      {
        strategies(first: 15, where: { whitelisted: true }) {
          id
          token {
            id
          }
          metadata
          whitelisted
          rates {
            id
            valueRate
            totalValue
            totalShares
            shareValue
            accumulatedShareValue
            
          }
          lastRate {
            id
            valueRate
            totalValue
            totalShares
            shareValue
            accumulatedShareValue
            
          }
          vault {
            id
          }
          token {
            id
          }
          metadata
        }
      }
    `
  );
  // console.log('useWhitelistedStrategies st',strategies);
  return strategies;
};

export default useStrategies;
