import { Web3Provider } from '@ethersproject/providers'
import { InjectedConnector } from '@web3-react/injected-connector'
import { NetworkConnector } from '@web3-react/network-connector'
import { getDefaultChainID, getNodeURI } from '../helpers/enviroment'
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";


const NETWORK_URL = getNodeURI()

const POLLING_INTERVAL = 12000;


export const walletconnect = new WalletConnectConnector({
  supportedChainIds: [137],
  rpc: {
    137: "https://rpc-mainnet.maticvigil.com",
  },
  bridge: "https://bridge.walletconnect.org",
  qrcode: true,
  pollingInterval: POLLING_INTERVAL
});

export const NETWORK_CHAIN_ID = parseInt(getDefaultChainID() ?? '1')

if ( NETWORK_URL === 'undefined') {
  throw new Error(`REACT_APP_NETWORK_URL must be a defined environment variable`)
}

export const network = new NetworkConnector({
  urls: { [NETWORK_CHAIN_ID]: NETWORK_URL },
})

let networkLibrary
export function getNetworkLibrary() {
  return (networkLibrary = networkLibrary ?? new Web3Provider(network.provider))
}

export const injected = new InjectedConnector({
  supportedChainIds: [1, 3, 4, 5, 42],
})
