import { useMemo } from "react";
import {
  ChainId,
  ERC20Interface,
  useContractFunction,
  useEthers,
} from "@usedapp/core";
import { VAULT_ADDRESS } from "../constants";
import useToken from "./useToken";
import { MaxInt256 } from "@ethersproject/constants";
import { Contract } from "@ethersproject/contracts";
import { isAddress } from "../helpers";
import { USDC_TOKEN } from "../constants/tokens";

// returns a function that will execute a join in strategy,
export function useApproveCallback(account, token = "") {
  const { chainId } = useEthers();
  const tokenInfo = useToken(token); // token info to get decimals
  // console.log(
  //   "useApproveCallback",
  //   token,
  //   tokenInfo,
  //   isAddress(tokenInfo[chainId])
  // );
  const tokenAddress = isAddress(tokenInfo[chainId])
    ? isAddress(tokenInfo[chainId])
    : USDC_TOKEN[ChainId.Polygon];

  const contract = new Contract(tokenAddress, ERC20Interface);

  // const txAmount = parseBN(amount, tokenInfo.decimals);
  // console.log("useApproveCallback contract",  tokenAddress,
  // ERC20Interface);

  const { state, send } = useContractFunction(contract, "approve", {
    transactionName: `Approve ${tokenInfo?.name}`,
  });

  return useMemo(() => {
    if (!token) {
      return { state: null, callback: null, error: "Missing dependencies" };
    }
    return {
      state: state,
      callback: async function onApprove() {
        return send(VAULT_ADDRESS[chainId], MaxInt256);
      },
    };
  }, [state, send, account, chainId]); // eslint-disable-line
}
