import {
  Flex,
  Text,
  Slider,
  SliderTrack,
  SliderFilledTrack,
  SliderThumb,
  Box,
  Spinner,
  Switch,
} from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { SETTINGS } from "../helpers/settings";
import { formatBNPrice, formatBNUnits, mulBN } from "../helpers";
import TokenInput from "./TokenInput";
import { FiArrowDown } from "react-icons/fi";
import { SimpleTitle } from "./common/BoxContainer";
import { StrategyTitle } from "./common/StrategyTitle";
import { LogoIcon } from "../assets/MimicIcons";
import useExitEstimation from "../hooks/useExitEstimation";
import { QUERY_STATUS } from "../constants";
import { useTranslation } from "react-i18next";
import { Hr } from "./strategy/Hr";
import { ShowFormError } from "./ShowFormError";
import { ShowAmountWithToken } from "./common/StrategyStats";
import useUSDCPrice from "../hooks/useUSDCPrice";
import { ChainId } from "@usedapp/core";
import useToken from "../hooks/useToken";

export default function ExitOptions({
  strategyAccountData,
  handleInputTokenChange,
  tokenAmounts,
  ratio,
  setRatio,
  queryCallback,
  queryState,
}) {
  const { t } = useTranslation();

  const [onlyGains, setOnlyGains] = useState(false);

  const sliderChange = (val) => {
    setRatio(val);
  };

  const withdrawToken = strategyAccountData?.strategy?.token?.id;

  const { same, amountIn, amountOut, isLoading, totalGains, totalRatioGains } =
    useExitEstimation(
      withdrawToken,
      strategyAccountData,
      ratio,
      tokenAmounts?.token,
      queryState,
      queryCallback
    );
  // console.log("useExitCallback 44", amountIn, amountOut);

  useEffect(() => {
    queryCallback();
  }, [same]); // eslint-disable-line

  useEffect(() => {
    if (onlyGains) {
      console.log("totalRatioGains", totalRatioGains);
      setRatio(totalRatioGains);
    } else {
      // reset position
      setRatio(1);
    }
  }, [onlyGains]); // eslint-disable-line

  return (
    <Flex direction="column" w="full">
      <StrategyTitle
        stId={strategyAccountData?.strategy?.id}
        tokenIn={strategyAccountData?.strategy?.token?.id}
        px="4"
        pt="4"
      />
      {totalGains && (
        <>
          <Hr />
          <Flex direction="column">
            <GainRow
              setOnlyGains={setOnlyGains}
              amount={totalGains}
              token={withdrawToken}
            />
          </Flex>
          <Hr />
        </>
      )}
      <Flex justify="space-between" align="center" px="4">
        <SimpleTitle>{t("amountToWithdraw")}</SimpleTitle>
        <Text fontWeight="600" pt="3">
          {mulBN(ratio, 100)}%
        </Text>
      </Flex>
      <Flex w="full" px="4" mb="6" mt="2">
        <SliderAmount
          isDisabled={onlyGains}
          onChange={sliderChange}
          queryCallback={queryCallback}
        />
      </Flex>
      <Flex direction="column" align="center" w="full" p="4">
        <TokenInput
          defaultToken={withdrawToken}
          defaultAmount={amountIn}
          address={strategyAccountData?.strategy?.token?.id}
          onChange={handleInputTokenChange}
          isChangeable={same}
          hideBalance={true}
          hintText="Withdraw"
        />
        {!same && (
          <>
            <Flex w="full" justify="center" pb="8">
              <FiArrowDown fontSize="1.25rem" />
            </Flex>

            <TokenInput
              defaultAmount={amountOut}
              address={strategyAccountData?.strategy?.token?.id}
              onChange={handleInputTokenChange}
              startToken={tokenAmounts.token}
              isChangeable={true}
              hideBalance={true}
              hintText="Receive"
              isLoading={isLoading}
            />
          </>
        )}
      </Flex>
      {!same && (
        <Flex w="full" justify="center" direction="column">
          <ShowFormError
            errorMessage={
              queryState.status === QUERY_STATUS.ERROR
                ? t("noEstimation")
                : undefined
            }
          />

          {isLoading && (
            <Flex w="full" justify="center">
              <Spinner my="2" mx="4" />
            </Flex>
          )}
        </Flex>
      )}
    </Flex>
  );
}

const SliderAmount = ({ onChange, queryCallback, isDisabled }) => {
  return (
    <Slider
      isDisabled={isDisabled}
      aria-label="slider-ex-4"
      defaultValue={1}
      min={0}
      max={1}
      step={0.001}
      mt="2"
      onChangeEnd={() => queryCallback()}
      onChange={(v) => onChange(v)}
    >
      <SliderTrack bg={SETTINGS.VIOLET_LIGHTER}>
        <SliderFilledTrack bg={SETTINGS.VIOLET_LIGHT} />
      </SliderTrack>
      <SliderThumb boxSize="6" bg={SETTINGS.VIOLET_DARK}>
        <Box color={SETTINGS.VIOLET_LIGHTER} as={LogoIcon} p="2px" />
      </SliderThumb>
    </Slider>
  );
};

const GainRow = ({ token, amount, isLoading, setOnlyGains }) => {
  const tk = useToken(token);
  const address = tk ? tk[ChainId.Mainnet] : token;
  const amountReal = formatBNUnits(amount, tk?.decimals);
  const amountNormalized = formatBNPrice(amount, tk?.decimals);
  const price = useUSDCPrice(address, amountNormalized);
  console.log("amount", amount);
  return (
    <Flex direction="column" px="6" w="full">
      <Flex align="center" pt="6" w="full">
        <SimpleTitle pb="0" pt="0">
          Gains
        </SimpleTitle>

        <Flex align="center" justify="flex-end" w="full">
          <Text mr="2">Exit only total gains</Text>
          <Switch
            colorScheme="green"
            size="md"
            onChange={(e) => setOnlyGains(e.target.checked)}
          />
        </Flex>
      </Flex>

      <Flex key={token} justify="space-between" flexWrap={"wrap"}>
        <ShowAmountWithToken price={amountReal} tk={tk} />
        <ShowAmountWithToken price={price} simbol="$" />
      </Flex>
    </Flex>
  );
};
