// We just default to staging by default
export const isDevelopment = () => !isStaging() && !isProduction();

// We use the same environment for development and staging
// But we still need a switch to only show dev features locally
export const isStaging = () => process.env.REACT_APP_VERCEL_ENV === "preview";

export const isProduction = () =>
  process.env.REACT_APP_VERCEL_ENV === "production";

export const getNodeURI = () =>
  isProduction()
    ? process.env.REACT_APP_PRODUCTION_URI
    : process.env.REACT_APP_PREVIEW_URI;

export const getSubgraphqlURI = () =>
  (isProduction()
    ? process.env.REACT_APP_PRODUCTION_SUBGRAPHQL_URL
    : process.env.REACT_APP_PREVIEW_SUBGRAPHQL_URL) ||
  "https://api.thegraph.com/subgraphs/name/mimic-fi/kovan";

export const getSubgraphqlBaseURI = () =>
  "https://api.thegraph.com/subgraphs/name";

export const getDefaultNetworkName = () =>
  isDevelopment() || isStaging()
    ? process.env.REACT_APP_PREVIEW_NETWORK_NAME
    : process.env.REACT_APP_PRODUCTION_NETWORK_NAME;

export const getDefaultChainID = () =>
  isProduction()
    ? Number(process.env.REACT_APP_PRODUCTION_CHAIN_ID)
    : Number(process.env.REACT_APP_PREVIEW_CHAIN_ID);

export const getVaultAddress = () =>
  isProduction()
    ? process.env.REACT_APP_PRODUCTION_VAULT_ADDRESS
    : process.env.REACT_APP_PREVIEW_VAULT_ADDRESS;

export const getAGreementFactoryAddress = () =>
  isProduction()
    ? process.env.REACT_APP_PRODUCTION_AGREEMENT_FACTORY_ADDRESS
    : process.env.REACT_APP_PREVIEW_AGREEMENT_FACTORY_ADDRESS;
