import { DEFAULT_INITIAL_PORTFOLIO } from "../constants";

export const ActionTypes = {
  ADD_PORTFOLIO: "ADD_PORTFOLIO",
  SUBSTRACT_PORTFOLIO: "SUBSTRACT_PORTFOLIO",
  SELECT_PORTFOLIO: "SELECT_PORTFOLIO",
  UPDATE_PORTFOLIO_BALANCE: "UPDATE_PORTFOLIO_BALANCE",
};

export const reducer = (state, action) => {
  // console.log('state', state);
  // console.log('action', action);
  switch (action.type) {
    case ActionTypes.UPDATE_PORTFOLIO_BALANCE:
      let update = { ...state.portfolioSelected?.balance };
      let gains = { ...state.portfolioSelected?.gains };
      let sumApy = { ...state.portfolioSelected?.sumApy };
      let invested = { ...state.portfolioSelected?.invested };
      update[action.balance.address] = action.balance.total;
      gains[action.balance.address] = action.balance.gains;
      sumApy[action.balance.address] = action.balance.sumApy;
      invested[action.balance.address] = action.balance.invested;
      return {
        ...state,
        portfolioSelected: {
          ...state.portfolioSelected,
          balance: { ...update },
          gains: { ...gains },
          sumApy: { ...sumApy },
          invested: { ...invested },
        },
      };
    case ActionTypes.ADD_PORTFOLIO:
      const exist = state?.portfolios?.find(
        (p) => p.address === action.portfolio.address
      );
      if (exist) {
        console.log(`${action.portfolio.address} already exist`);
        return {
          ...state,
        };
      } else {
        console.log(`${action.portfolio.address} its new`);
        const select_new_portfolio = true;
        const portfolio = { address: action.portfolio.address, name: "" };
        const portfolioSelected = select_new_portfolio
          ? action.portfolio.address
          : state.portfolioSelected.address;

        // console.log('portfolioSelected', portfolioSelected);
        return {
          ...state,
          portfolios: [...state.portfolios, portfolio],
          portfolioSelected: {
            ...DEFAULT_INITIAL_PORTFOLIO.portfolioSelected,
            address: portfolioSelected,
          },
        };
      }

    case ActionTypes.SUBSTRACT_PORTFOLIO:
      const portfolios = state.portfolios.filter(
        (x) => x.address !== action.portfolio.address
      );
      return { ...state, portfolios: portfolios };
    case ActionTypes.SELECT_PORTFOLIO:
      return {
        ...state,
        portfolioSelected: {
          ...DEFAULT_INITIAL_PORTFOLIO.portfolioSelected,
          address: action.portfolio,
        },
      };
    default:
      return state;
  }
};
